<template>
  <div class="theDetailBody">
    <div class="user-header">
      <a class="nav-item1" @click="theDetailBodyBack"><img src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="personinfo">我的钻石明细</a>
      <select id="sel" @change="setDays">
          <option :value="index" v-for="(item,index) in chooseList">{{item}}</option>
      </select>
    </div>
    <div>
      <ul class="mingxiList">
        <li v-for="item in payOrders">
          <p>
            <a>{{item.remark}}</a><br/>
            <span>{{item.time}}</span>
          </p>
          <div>
            <a class="tenNum">{{item.size}}</a>
            <span>钻</span>
          </div>
        </li>
      </ul>
      <div class="loadMore" @click="getMore" v-show="isMore">点击加载更多</div>
      <div class="noMore" v-show="!isMore">没有更多记录了</div>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;

  export default {
    data() {
      return {
        payOrders:[],  //显示的数据
        isMore:true,   //更多按钮
        clickNum:1,    //记录点击次数，获取更多数据
        chooseList:["当日明细","最近三日","最近七日"],   //选择查看时间
        id:null,
        isRecord:null,
      }
    },
    mounted:function () {
      var _this = this;
      var userid = getCookie("userId");
      $.ajax({
        type: 'GET',
        url: urlhost+"/usercenter/expense?time=0&user_id="+userid,
        data$Type: "jsonp",
        data: {
        },
        success: function (response){
          var mess = JSON.parse(response);
          var info = mess.message;
          var mode = response.mode;

          //判断是否为空数据
          if(info == ''){
            _this.isRecord = false;
          }else{

            //循环处理时间，钻石类型，明细类型
            for(var i=0;i< info.length;i++){
              var num = parseInt(info[i].time);
              info[i].time = _this.format(num);

              //判断钻石明细类型
              if(info[i].mode == 1){
                info[i].size = "+"+info[i].size;
              }else{
                info[i].size = -info[i].size;
              }
            }
            _this.payOrders = info;
          }

          //判断获取的数组长度，小于10条时没有更多框
          if(info.length<10){
            _this.isMore = false;
          }else{
            _this.isMore = true;
          }
        },
      });
    },
    methods:{
      theDetailBodyBack:function () {
        this.$router.go(-1);
      },

      //获取时间
      format:function(timestamp){
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
        return Y+M+D+h+m;
      },
      //选择天数范围
      setDays:function(){

        var i = $('#sel>option:selected').val();
        var id;
        if(i == 0){
          this.id = 0;
        }else if(i == 1){
          this.id = 3;
        }else if(i == 2){
          this.id = 7;
        };

        var _this = this;
        var userId = getCookie("userId");

        //带上参数，用户id、每页条数num、选择区间time、页数page
        var url= urlhost+"/usercenter/expense?user_id="+userId+"&num=999&time="+_this.id;

        $.get(url).then(function(res){
          var mess = JSON.parse(res);
          var info = mess.message;
          var mode = mess.mode;
          console.log(_this.id)

          for(var i=0;i< info.length;i++) {
            var num = parseInt(info[i].time);
            info[i].time = _this.format(num);

            //判断钻石明细类型
            if (info[i].mode == 1) {
              info[i].size = "+" + info[i].size;
            } else {
              info[i].size = -info[i].size;
            }
          };

          _this.isMore = false;
          _this.payOrders = info;
        },function(res){});

        // var height = parseInt($('.mingxiList li').css('height'));
        // console.log($('.mingxiList li').length)
        $('.mingxiList').animate({'height':'100%',scrollTop:0},500);

      },
      //点击更多按钮
      getMore:function(){
        this.clickNum++;
        var page = this.clickNum;
        var userid = getCookie("userId");
        var url = urlhost+"/usercenter/expense";

        $.post(url,{"page":page,"user_id":userid},{emulateJSON:true}).then((res)=>{
          var mess = JSON.parse(res);
          var info = mess.message;
          var mode = mess.mode;

          //判断请求状态为0时 或者 请求的数组长度小于等于10时
          if(mess.status == 0 || info.length<=10){
            this.isMore = false;
          }
          for(var i=0;i< info.length;i++) {
            var num = parseInt(info[i].time);
            info[i].time = this.format(num);

            //判断钻石明细类型
            if (info[i].mode == 1) {
              info[i].size = "+" + info[i].size;
            } else {
              info[i].size = -info[i].size;
            }
            this.payOrders.push(info[i]);
          };

          console.log(info)

          /*//判断获取的数组长度，小于10条时没有更多框
          if(info.length<10){
            this.isMore = false;
          }else{
            this.isMore = true;
          }*/

        }).catch((res)=>{})
      },

    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .theDetailBody{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: scroll;
    top: 0px;
    color: #585858;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      line-height: n(80);
      z-index: 999;
      border-bottom: n(1) solid #d8d8d8;
      .nav-item1{
        position: absolute;
        left: 0.2rem;
        img{
          width: n(50);
          height: n(50);
        }
      }
      .personinfo{
        font-size: n(14);
        font-weight: bold;
        width: 100%;
        display: inline-block;
        text-align: center;
      }
      select{
        width: n(130);
        position: absolute;
        font-size: n(12);
        right: n(10);
        top: 30%
      }
    }
    .noRecord{
      text-align: center;
      margin-top: n(80);
      height: 100%;
      margin-top: 50%;
    }
    .mingxiList{
      width: 100%;
      height: auto;
      margin-top: n(80);
      overflow: auto;
      li{
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: n(90);
        border-bottom: n(1) solid #e4ece1;
        div{
          width: n(150);
          margin-right: n(25);
          text-align: right;
        }
        a{
          color: #585858;
          line-height: n(30);
        }
        p{
          line-height: n(30);
          margin-left: n(25);
        }
      }
    }
    .loadMore{
      height:n(80);
      line-height: n(80);
      width: 100%;
      text-align: center;
      color: #1b1a1a;
      background: #fff001;
      font-weight: bold;
    }
    .noMore{
      height: n(50);
      line-height: n(50);
      width: 100%;
      text-align: center;
      color: #666;
    }
  }
</style>




